body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

#root {
  width: 400px;
  min-width: 400px;
  max-width: 400px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #ffce9b;
  border: 1px solid #000;
  float: left;
  font-size: 35px;
  font-weight: bold;
  line-height: 51px;
  height: 51px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 51px;
}

.black-square {
  background: #d28b3f;
}

.selected {
  background: #606dbc;
}

.selected.black-square  {
  background: #465298;
}

.valid-move {
  background: -webkit-repeating-radial-gradient(
    circle,
    #465298,
    #465298 5px,
    #606dbc 24px,
    #606dbc 240px
  );
  background: -o-repeating-radial-gradient(
    circle,
    #465298,
    #465298 5px,
    #606dbc 24px,
    #606dbc 240px
  );
  background: repeating-radial-gradient(
    circle,
    #465298,
    #465298 5px,
    #606dbc 24px,
    #606dbc 240px
  );
}

.valid-move.black-square {
  background: -webkit-repeating-radial-gradient(
    circle,
    #606dbc,
    #606dbc 5px,
    #465298 24px,
    #465298 240px
  );
  background: -o-repeating-radial-gradient(
    circle,
    #606dbc,
    #606dbc 5px,
    #465298 24px,
    #465298 240px
  );
  background: repeating-radial-gradient(
    circle,
    #606dbc,
    #606dbc 5px,
    #465298 24px,
    #465298 240px
  );
}

.capture-cell {
  background: -webkit-repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
  background: -o-repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
  background: repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
}

.capture-cell.black-square {
  background: -webkit-repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
  background: -o-repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
  background: repeating-radial-gradient(
    circle,
    #f44,
    #f44 5px,
    #800 24px,
    #800 240px
  );
}

.square:focus {
  outline: none;
}



